.current {
  &-events {
    padding: 0 0 60px;

    @include onTablet {
      padding-bottom: 112px;
    }

    &__title {
      margin: 0;
      font-family: $font-titel;
      font-style: normal;
      font-weight: 700;
      font-size: 36px;
      line-height: 100%;
      color: #0f0e08;
      text-align: center;

      @include onTablet {
        text-align: left;
        font-size: 48px;
        margin-bottom: 48px;
      }
    }

    &__bottom-intend {
      margin-bottom: 60px;

      @include onTablet {
        margin-bottom: 64px;
      }
    }
  }
}
